import React from 'react';
import { useHistory } from 'react-router';
// MUI Components
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box'; 
// Page component
import SuccessDialog from '../shared/Dialog/SuccessDialog';
// react-icons
import { FaRegCheckCircle, FaSignInAlt } from 'react-icons/fa';

export default function SuccessModal(props) {

    const history = useHistory();

    return (
        <div>
            <SuccessDialog open={props.open} toggle={props.toggle} size="md" goto={props.goto}>
                <Box display="flex" flexDirection="column" justifyContent="center" alignItems="center" sx={{ p: 2 }}>
                    <Box display="flex" flexDirection="row" justifyContent="center" sx={{ mt: 0 }}>
                        <Typography color="#00BFA5" variant="h3" sx={{ mr: 2 }}>Registration Complete</Typography>
                        <FaRegCheckCircle color="#00BFA5" size={40} style={{ marginTop: 10 }} />
                    </Box>
                    <Typography variant="h5" sx={{ mt: 3 }}>Welcome to Bangko sa Balay Foundation Inc.</Typography>
                    <Typography variant="body1" sx={{ mt: 1 }}>Thank you for choosing us.</Typography>
                    <Button variant="contained" color="primary" sx={{ mt: 3 }} onClick={() => history.push('/login')}>Login<FaSignInAlt style={{ marginLeft: 5 }} /> </Button>

                </Box>
            </SuccessDialog>
        </div>
    )
}
