import * as React from 'react';

// MUI Component
import Dialog from '@mui/material/Dialog';
import DialogContent from '@mui/material/DialogContent'; 

export default function OpenAccountDialog(props) {

    return (
        <div>
            <Dialog
                open={props.open}
                disablePortal
                disableScrollLock
                maxWidth="sm"
                fullScreen={false}
                onBackdropClick={() => props.toggle()}
                onClose={() => props.toggle()}
                sx={{
                    zIndex: 150,
                }}
            >
                <DialogContent sx={{ maxHeight: '100vh' }}>
                    {props.children}
                </DialogContent>
            </Dialog>
        </div >
    );

}
