import React from 'react'
import toastService from '../../../services/toast/toastService'; 

// repository
import authRepository from '../../../repositories/authRepository';

// MUI Components
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import LoadingButton from '@mui/lab/LoadingButton';
import InputAdornment from "@mui/material/InputAdornment";
import IconButton from "@mui/material/IconButton";
import Divider from '@mui/material/Divider';
import Typography from '@mui/material/Typography';
import Link from '@mui/material/Link'; 
import Paper from '@mui/material/Paper';

// Page components
import Birthdate from '../../components/forms/fields/Birthdate';
import Gender from '../../components/forms/fields/Gender';
import Nationality from '../../components/forms/fields/Nationality';
import RegisterCover from '../../../../assets/register-cover.png';
import ContactNumber from './fields/ContactNumber';
import CivilStatus from './fields/CivilStatus';
import SuccessModal from '../modals/SuccessModal';
// react-icons
import { FaArrowLeft, FaArrowRight, FaCheck, FaUserPlus } from 'react-icons/fa';
import { MdVisibility, MdVisibilityOff } from 'react-icons/md';


export default function NewAccountForm() {

    const [loading, setLoading] = React.useState(false);
    const [step, setStep] = React.useState(0);
    const [showSuccessModal, setShowSuccessModal] = React.useState(false);
    const toggleSuccessModal = () => setShowSuccessModal(!showSuccessModal);
    // Basic Profile  
    const [lastName, setLastName] = React.useState("");
    const [firstName, setFirstName] = React.useState("");
    const [middleName, setMiddleName] = React.useState("");
    const [nameSuffix, setNameSuffix] = React.useState("");
    const [gender, setGender] = React.useState("");
    const [birthdate, setBirthdate] = React.useState(null);
    const [civilStatus, setCivilStatus] = React.useState("");
    const [nationality, setNationality] = React.useState("Filipino");
    const [contactNumber, setContactNumber] = React.useState(null);
    const [fbLink, setFbLink] = React.useState("");

    // Account
    const [email, setEmail] = React.useState("");
    const [code, setCode] = React.useState("");
    const [password, setPassword] = React.useState("");
    const [repassword, setRePassword] = React.useState("");
    const [isShowPassword, setIsShowPassword] = React.useState(false);
    const toggleIsShowPassword = () => setIsShowPassword(!isShowPassword);
    const [isShowRePassword, setIsShowRePassword] = React.useState(false);
    const toggleIsShowRePassword = () => setIsShowRePassword(!isShowRePassword);


    const handleStep = (step) => {
        setStep(step)
    }

    const handleSubmitEmail = async (e) => {
        try {
            e.preventDefault();

            if (password !== repassword) {
                toastService.loginError('Password doesn\'t match');
                return false;
            }

            let data = {
                email
            }

            setLoading(true);
            let result = await authRepository.sendCode(data);
            if (result.data.status) {
                handleStep(1);
            } else {
                toastService.loginError("Invalid Input");
            }

        } catch (error) { 
            if (error.response.data?.errors?.email[0] !== null) {
                toastService.loginError(error.response.data?.errors?.email[0]);
            } else {
                toastService.loginError(error.response.data.message);
            }
        }

        setLoading(false);
    }

    const handleCode = async (e) => {
        try {
            e.preventDefault();

            let data = {
                code,
                email
            }

            setLoading(true);
            let result = await authRepository.verify(data);
            if (result.data.status) {
                handleStep(2);
            } else {
                toastService.loginError("Code doesn't match");
            }

        } catch (error) {
            toastService.loginError(error.response.data.message);
        }

        setLoading(false);

    }

    const handleRegister = async (e) => {
        try {
            e.preventDefault();

            setLoading(true);

            let data = {
                password,
                password_confirmation: repassword,
                email,
                lastName,
                firstName,
                middleName,
                nameSuffix,
                gender,
                civilStatus,
                birthdate,
                nationality,
                contactNumber,
                fbLink
            }

            let result = await authRepository.register(data);
            if (result.data.status) {
                toggleSuccessModal();
            }

        } catch (error) {
            toastService.loginError("Invalid input");
        }

        setLoading(false);
    }

    return (
        <div style={{ width: '100%' }}>
            {showSuccessModal && <SuccessModal open={showSuccessModal} toggle={toggleSuccessModal} goto="/login" />}
            <Box sx={{ minHeight: 400 }}>
                <Grid container spacing={2}>
                    {step !== 3 &&
                        <React.Fragment>
                            <Grid item xs={12} md={7}>
                                <Paper elevation={0} sx={{
                                    backgroundColor: "transparent !important",
                                    backgroundImage: `url(${RegisterCover})`,
                                    backgroundSize: "500px 400px",
                                    backgroundPosition: "center",
                                    backgroundRepeat: "no-repeat",
                                    width: 600,
                                    height: 400
                                }}>
                                </Paper>
                            </Grid>
                            <Grid item xs={12} md={5}>
                                <Box sx={{ mb: 2, pl: 1, textAlign: 'center' }}>
                                    <Typography variant="h5" color="primary"><FaUserPlus /> Create New Account</Typography>
                                </Box>
                                <Divider />
                                {step === 0 &&
                                    <form onSubmit={(e) => handleSubmitEmail(e)} autoComplete="off">
                                        <Box sx={{ mx: 'auto', width: '100%', mt: 2 }}>
                                            <TextField
                                                sx={{ m: 1 }}
                                                onChange={(e) => setEmail(e.target.value)}
                                                value={email}
                                                variant="outlined"
                                                size="large"
                                                required
                                                fullWidth
                                                id="email"
                                                label="Email"
                                                name="email"
                                                type="email"
                                                helperText="Note: Use your active email for code verification"
                                                autoFocus
                                            />
                                            <TextField
                                                sx={{ m: 1 }}
                                                type={isShowPassword ? "text" : "password"}
                                                onChange={(e) => { setPassword(e.target.value); }}
                                                value={password}
                                                variant="outlined"
                                                size="large"
                                                margin="normal"
                                                required
                                                fullWidth
                                                name="password"
                                                label="Password"
                                                id="password"
                                                InputProps={{
                                                    endAdornment: (
                                                        <InputAdornment position="end">
                                                            <IconButton onClick={toggleIsShowPassword} size="small" sx={{ mr: -0.5 }}>
                                                                {isShowPassword ?
                                                                    <MdVisibility size={18} />
                                                                    :
                                                                    <MdVisibilityOff size={18} />
                                                                }
                                                            </IconButton>
                                                        </InputAdornment>
                                                    ),
                                                }}
                                            />
                                            <TextField
                                                sx={{ m: 1 }}
                                                type={isShowRePassword ? "text" : "password"}
                                                onChange={(e) => { setRePassword(e.target.value); }}
                                                value={repassword}
                                                variant="outlined"
                                                size="large"
                                                margin="normal"
                                                required
                                                fullWidth
                                                name="repassword"
                                                label="Re-enter Password"
                                                id="repassword"
                                                InputProps={{
                                                    endAdornment: (
                                                        <InputAdornment position="end">
                                                            <IconButton onClick={toggleIsShowRePassword} size="small" sx={{ mr: -0.5 }}>
                                                                {isShowRePassword ?
                                                                    <MdVisibility size={18} />
                                                                    :
                                                                    <MdVisibilityOff size={18} />
                                                                }
                                                            </IconButton>
                                                        </InputAdornment>
                                                    ),
                                                }}
                                            />
                                            <Grid container spacing={1} sx={{ m: 1 }}>
                                                <Grid item xs={12} sx={{ textAlign: 'center' }}>
                                                    <LoadingButton
                                                        sx={{ p: 1.5, mt: 1, width: 300 }}
                                                        size="large"
                                                        type="submit"
                                                        fullWidth
                                                        variant="contained"
                                                        color="primary"
                                                        loading={loading}
                                                    >
                                                        Next
                                                        <FaArrowRight style={{ marginLeft: 5 }} />
                                                    </LoadingButton>
                                                </Grid>
                                            </Grid>
                                        </Box>
                                    </form>
                                }
                                {step === 1 &&
                                    <form onSubmit={(e) => handleCode(e)} autoComplete="off">
                                        <Box sx={{ mx: 'auto', width: '100%', textAlign: 'center' }}>
                                            <Typography sx={{ mt: 3, ml: 1 }}>We send a verification code to your email</Typography>
                                            <TextField
                                                sx={{ m: 1, mt: 2 }}
                                                onChange={(e) => setCode(e.target.value)}
                                                value={code}
                                                variant="outlined"
                                                size="large"
                                                required
                                                fullWidth
                                                id="code"
                                                label="Verification Code"
                                                name="code"
                                                type="code"
                                                helperText="Note: Enter the latest verification code sent to your email"
                                                autoFocus
                                            />

                                            <Grid container spacing={1} sx={{ m: 1 }}>
                                                <Grid item xs={12} sx={{ textAlign: 'center' }}>
                                                    <Box display="flex" flexDirection="row" justifyContent="center">
                                                        <LoadingButton
                                                            sx={{ p: 1.5, mt: 3, mr: 1.5, width: 150 }}
                                                            size="large"
                                                            fullWidth
                                                            variant="contained"
                                                            color="primary"
                                                            loading={loading}
                                                            onClick={() => handleStep(0)}
                                                        >
                                                            <FaArrowLeft style={{ marginRight: 5 }} />
                                                            Back
                                                        </LoadingButton>
                                                        <LoadingButton
                                                            sx={{ p: 1.5, mt: 3, width: 150 }}
                                                            size="large"
                                                            type="submit"
                                                            fullWidth
                                                            variant="contained"
                                                            color="primary"
                                                            loading={loading}
                                                        >
                                                            Next
                                                            <FaArrowRight style={{ marginLeft: 5 }} />
                                                        </LoadingButton>
                                                    </Box>
                                                </Grid>
                                            </Grid>
                                        </Box>
                                    </form>
                                }
                                {step === 2 &&
                                    <form onSubmit={(e) => handleRegister(e)} autoComplete="off">
                                        <Box sx={{ mx: 'auto', width: '100%', mt: 2 }}>
                                            <TextField
                                                sx={{ m: 1 }}
                                                onChange={(e) => setLastName(e.target.value)}
                                                value={lastName}
                                                variant="outlined"
                                                size="large"
                                                required
                                                fullWidth
                                                id="lastName"
                                                label="Last Name"
                                                name="lastName"
                                                autoFocus
                                            />
                                            <TextField
                                                sx={{ m: 1 }}
                                                onChange={(e) => setFirstName(e.target.value)}
                                                value={firstName}
                                                variant="outlined"
                                                size="large"
                                                required
                                                fullWidth
                                                id="firstName"
                                                label="First name"
                                                name="firstName"
                                            />
                                            <TextField
                                                sx={{ m: 1 }}
                                                onChange={(e) => setMiddleName(e.target.value)}
                                                value={middleName}
                                                variant="outlined"
                                                size="large"
                                                fullWidth
                                                id="middleName"
                                                label="Middle name"
                                                name="middleName"
                                                helperText="(Optional)"
                                            />
                                            <TextField
                                                sx={{ m: 1 }}
                                                onChange={(e) => setNameSuffix(e.target.value)}
                                                value={nameSuffix}
                                                variant="outlined"
                                                size="large"
                                                fullWidth
                                                id="nameSuffix"
                                                label="Name Suffix"
                                                placeholder="example Jr., Sr., III"
                                                name="nameSuffix"
                                                helperText="(Optional)"
                                            />
                                            <Box sx={{ m: 1, width: '100%' }}>
                                                <Birthdate birthdate={birthdate} setBirthdate={setBirthdate} size="large" />
                                            </Box>
                                            <Box sx={{ m: 1, mt: 2, width: '100%' }}>
                                                <Gender gender={gender} setGender={setGender} size="large" />
                                            </Box>
                                            <Box sx={{ m: 1, mt: 2, width: '100%' }}>
                                                <CivilStatus setCivilStatus={setCivilStatus} civilStatus={civilStatus} size="large" />
                                            </Box>
                                            <Box sx={{ m: 1, mt: 2, width: '100%' }}>
                                                <TextField
                                                    onChange={(e) => setContactNumber(e.target.value)}
                                                    value={contactNumber}
                                                    variant="outlined"
                                                    size="large"
                                                    required
                                                    fullWidth
                                                    id="contactNumber"
                                                    label="Mobile Number"
                                                    name="contactNumber"
                                                    InputProps={{
                                                        inputComponent: ContactNumber
                                                    }}
                                                />
                                            </Box>
                                            <Box sx={{ m: 1, mt: 2, width: '100%' }}>
                                                <Nationality nationality={nationality} setNationality={setNationality} size="large" />
                                            </Box>
                                            <TextField
                                                sx={{ m: 1 }}
                                                onChange={(e) => setFbLink(e.target.value)}
                                                value={fbLink}
                                                variant="outlined"
                                                size="large"
                                                fullWidth
                                                id="fblink"
                                                label="FaceBook Link"
                                                name="fblink"
                                                required
                                            />
                                            <Grid container spacing={1} sx={{ m: 1 }}>
                                                <Grid item xs={12}>
                                                    <Box sx={{ backgroundColor: "#FCFCFC", textAlign: 'center', mt: 2 }}>
                                                        By clicking create, you agree to our
                                                        <Link rel="noopener" href="#" sx={{ pl: 0.5, pr: 0.5 }}>Terms</Link> and
                                                        <Link rel="noopener" href="#" sx={{ pl: 0.5, pr: 0.5 }}>Conditions</Link>, and that you have read our
                                                        <Link rel="noopener" href="#" sx={{ pl: 0.5, pr: 0.5 }}>Privacy</Link> and
                                                        <Link rel="noopener" href="#" sx={{ pl: 0.5, pr: 0.5 }}>Cookie</Link>Policy.
                                                    </Box>
                                                </Grid>
                                                <Grid item xs={12} sx={{ textAlign: 'center' }}>
                                                    <Box display="flex" flexDirection="row" justifyContent="center">
                                                        <LoadingButton
                                                            sx={{ p: 1.5, mt: 3, mr: 1.5, width: 150 }}
                                                            size="large"
                                                            fullWidth
                                                            variant="contained"
                                                            color="primary"
                                                            loading={loading}
                                                            onClick={() => handleStep(1)}
                                                        >
                                                            <FaArrowLeft style={{ marginRight: 5 }} />
                                                            Back
                                                        </LoadingButton>
                                                        <LoadingButton
                                                            sx={{ p: 1.5, mt: 3, width: 150 }}
                                                            size="large"
                                                            type="submit"
                                                            fullWidth
                                                            variant="contained"
                                                            color="primary"
                                                            loading={loading}
                                                        >
                                                            Create
                                                            <FaCheck style={{ marginLeft: 5 }} />
                                                        </LoadingButton>
                                                    </Box>
                                                </Grid>
                                            </Grid>
                                        </Box>
                                    </form>
                                }
                            </Grid>
                        </React.Fragment>
                    } 
                </Grid>
            </Box>
        </div >
    )
}
