import React from 'react';

// MUI Components
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';

export default function About() {
    return (
        <div style={{ minHeight: 650, paddingTop: 50, paddingBottom: 150, backgroundColor: "#FCFCFC" }}>
            <div className="container">
                <Typography variant="h4" sx={{ mb: 1 }}>About us</Typography>
                <Divider sx={{ mb: 8}} />
                <Box>
                    <Typography variant="body1" textAlign="justify" fontSize={20}>
                        Bangko Sa Balay Foundation Inc.(BBFI) is a microfinance institution created under the Balay Mindanaw Group of NGOs.
                        It is duly registered with the Securities and Exchange Commission (SEC) under certification number CN200730600 issued on October 16, 2007.
                        Aside from its community-based financial literacy and microfinance operations, it also serves as a project partner to other members of the Balay Mindanaw Group,
                        specifically Katilingbanong Pamahandi sa Mindanaw Foundation, Inc. (KPMFI) in its backyard broiler production project and in serving microenterpreneurs in Gingoog and Cagayan de Oro City.

                    </Typography>
                    <Typography variant="body1" textAlign="justify" fontSize={20} sx={{ mt: 7 }}>
                        BBFI implements the SURE model (Synergizing Urban and Rural Entrepreneurs) in its operations.
                    </Typography>
                </Box>
            </div>
        </div>
    )
}
